import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, reduxForm } from "redux-form";
import { ReanderField } from "../../components/helpers/field";
import ValidasiLogin from "../../Validasi/ValidasiLogin";
import ScrollAnimation from "react-animate-on-scroll";
import "animate.css/animate.min.css";
import LoadingButton from "../../components/helpers/function";
// const AppName = process.env.REACT_APP_NAME;

class FormLogin extends Component {
  state = {};

  componentDidMount() {
    setTimeout(() => {
      document.getElementById("user_id").focus()
    }, 100);
  }
  render() {
    return (
      <ScrollAnimation animateIn="flipInY">
        <div className="login-header">
          <div className="brand">
            <span className="logo"></span> {this.props.nama_toko}
          </div>
          <div className="icon">
            <i className="fa fa-sign-in"></i>
          </div>
          <div className="login-content">
            <form
              className="margin-bottom-0"
              onSubmit={this.props.handleSubmit}
              autoComplete="off"
            >
              <div className="form-group m-b-15">
                {/* <input type="text" className="form-control form-control-lg" placeholder="Email Address" required /> */}
                <Field
                  id="user_id"
                  name="user_id"
                  component={ReanderField}
                  type="text"
                  nouperCase
                  label="Username"
                  placeholder="Masukan Username"
                />
              </div>

              <div className="form-group m-b-15">
                <Field
                  name="password"
                  component={ReanderField}
                  type="password"
                  nouperCase
                  label="Password"
                  placeholder="Masukan Password"
                />
                {/* <input type="password" className="form-control form-control-lg" placeholder="Password" required /> */}
              </div>
              {/* <i className="fas fa-spinner fa-spin"></i> */}
              <div className="login-buttons">
                {this.props.isLoading ? (
                  <LoadingButton
                    className="btn btn-success btn-block btn-lg"
                    text="Sedang Login"
                  />
                ) : (
                  <>
                    <button
                      type="submit"
                      className="btn btn-success btn-block btn-lg"
                    >
                      Login
                    </button>
                  </>
                )}
              </div>

              <hr />
              <p className="text-center text-grey-darker">
                &copy; {this.props.nama_toko} All Right Reserved{" "}
                {new Date().getFullYear()}(30/06/2021)
                <br />
                V.1.0
              </p>
            </form>
          </div>
        </div>
      </ScrollAnimation>
    );
  }
}

// export default FormLogin;
FormLogin = reduxForm({
  form: "FormLogin",
  enableReinitialize: true,
  validate: ValidasiLogin,
})(FormLogin);
export default connect()(FormLogin);
